.Header,
.HeaderMobile {
    background-color: #404040;
    /* background-color: #333333; */
    height:61.33px;
    /* opacity: 0.5; */
}

/* .Header p,
.HeaderMobile p {
    color:#404040;
} */

.Header ul,
.HeaderMobile ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

.Header li,
.HeaderMobile li {
    display: inline-block;
    padding:20px 30px 20px 30px;
    color:white;
    /* color:#404040; */
}

.Header li:hover,
.HeaderMobile li:hover,
.Header li:active,
.HeaderMobile li:active {
    background-color: #1a1a1a;
}

.Header a,
.HeaderMobile a {
    text-decoration: none;
    color:white;
}

.fa-bars {
    font-size: 18pt;
    color: #e6e6e6;
}

.menuIcon,
.menuIconClicked {
    border:none;
    padding:0px;
    margin:0px;
    width:61.3px;
    height:61.3px;
    background-color: #404040;
    display:none;
}

.menuIcon:hover,
.menuIconClicked:hover {
    background-color: #1a1a1a;
}

.menuIcon:focus,
.menuIconClicked:focus {
    outline: none;
  }

@media screen and (max-width: 630px) {
    .menuIcon,
    .menuIconClicked {
        display:block;
    }

    .Header ul {
        display: none;
    }

    .menuIconClicked {
        background-color: #1a1a1a;
    }

    .HeaderMobile li {
        background-color: #1a1a1a;
        display:block;
        width:90px;
    }

    .HeaderMobile li:hover {
        background-color: #404040;
    }
}
